import pen from './img/pen.png';
import eraser from './img/Eraser.png';
import paint from './img/paint.png';

export default function AppView() {
  const main = document.createElement('main');
  main.innerHTML = '<div class = main-header><h1>PISKEL-CLONE</h1></div><div class = main-header_box><div class =main-header_box--tool></div><div class = main-header_box--frames><ul><li></li></ul><div id=add_frame>ADD NEW   FRAME</div><div class = duplicate_frame>DUPLICATE CURRENT FRAME</div></div><div class =main-header_box--canvas><canvas id=canvas></canvas></div><div class =main-header_box--animation></div></div>';
  const body = document.querySelector('body');
  body.appendChild(main);
  const toolBox = document.querySelector('.main-header_box--tool');
  const img1 = new Image();
  const img2 = new Image();
  const img3 = new Image();
  const color = document.createElement('div');
  color.id = 'color';
  color.innerHTML = '<div id = color1></div><div id = color2 ></div><div id = color3></div><div id = color4></div><div id = color5></div><div id = color6></div><div id = color7></div><div id = color8></div><div id = color9></div><div id = color10></div>';
  img1.src = pen;
  toolBox.appendChild(img1);
  img2.src = eraser;
  toolBox.appendChild(img2);
  img3.src = paint;
  toolBox.appendChild(img3);

  toolBox.appendChild(color);
  let selectedImg;
  function highlight(node) {
    if (selectedImg) {
      selectedImg.classList.remove('highlight');
    }
    selectedImg = node;
    selectedImg.classList.add('highlight');
  }


  function choiseTool() {
    toolBox.onclick = function EV(event) {
      const targ = event.target;


      if (targ.tagName !== 'IMG') return;

      highlight(targ);
    };
  }
  choiseTool();
  function addFrame() {
    const addFr = document.querySelector('#add_frame');
    addFr.onclick = function EV() {
      const UL = document.querySelector('.main-header_box--frames ul');
      const il = document.createElement('li');
      il.innerHTML = '<p>Delete</p>';
      UL.appendChild(il);
      if (document.querySelectorAll('.main-header_box--frames ul li').length === 2) {
        const del = document.createElement('p');
        del.innerHTML = 'Delete';
        document.querySelector('.main-header_box--frames ul li').insertBefore(del, document.querySelector('.main-header_box--frames ul li img'));
      }
    };
  }
  addFrame();
  function deleteFrame() {
    const framesBox = document.querySelector('.main-header_box--frames');
    framesBox.onclick = function EV(event) {
      const targ = event.target;
      if (targ.tagName !== 'P') return;
      const frame = targ.parentNode;
      frame.remove();
      if (document.querySelectorAll('.main-header_box--frames ul li').length === 1) {
        document.querySelector('.main-header_box--frames ul li p').remove();
      }
    };
  }
  deleteFrame();
}
