export default function AppModel() {
  const canvas = document.querySelector('#canvas');
  const ctx = canvas.getContext('2d');
  canvas.width = 800;
  canvas.height = 800;
  let ismouse = false;
  function pen() {
    canvas.addEventListener('mousedown', () => {
      ismouse = true;
    });
    canvas.addEventListener('mouseup', () => {
      ismouse = false;
      ctx.beginPath();
    });
    canvas.addEventListener('mousemove', (e) => {
      const penBottom = document.getElementsByTagName('IMG')[0];
      if (ismouse && penBottom.matches('.highlight')) {
        ctx.beginPath();
        ctx.fillRect(e.pageX - 680, e.pageY - 120, 25, 25);
      }
    });
  }
  pen();
  function eraser() {
    canvas.addEventListener('mousemove', (e) => {
      const eraserBottom = document.getElementsByTagName('IMG')[1];
      if (ismouse && eraserBottom.matches('.highlight')) {
        ctx.clearRect(e.pageX - 680, e.pageY - 120, 25, 25);
      }
    });
  }
  eraser();
  let selectedFrame;
  function frameChoisen(node) {
    if (selectedFrame) {
      selectedFrame.classList.remove('frameChoisen');
    }
    selectedFrame = node;
    selectedFrame.classList.add('frameChoisen');
    const img = document.querySelector('.frameChoisen img');

    img.onload = () => {
      ctx.drawImage(img, 0, 0, 800, 800);
    };


    console.log();
  }
  function choiseFrame() {
    const frameBox = document.querySelector('.main-header_box--frames');
    frameBox.addEventListener('mouseup', (event) => {
      const targ = event.target;
      if (targ.tagName !== 'LI') return;
      frameChoisen(targ);
    });
  }
  choiseFrame();
  function getImg() {
    canvas.addEventListener('mouseup', () => {
      const currentFrame = document.querySelector('.frameChoisen');
      const img = new Image();
      img.src = document.querySelector('#canvas').toDataURL();
      if (currentFrame) {
        const currentImg = document.querySelector('.frameChoisen img');
        if (currentImg) {
          currentImg.remove();
        }
        currentFrame.appendChild(img);
        return;
      }
      const frame = document.querySelector('ul li:last-child');
      const currentImg = document.querySelector('ul :last-child img');
      if (currentImg) {
        currentImg.remove();
      }
      frame.appendChild(img);
    });
  }
  getImg();
  function paintBucket() {
    canvas.addEventListener('mousedown', () => {
      const paintbucketBottom = document.getElementsByTagName('IMG')[2];
      if (paintbucketBottom.matches('.highlight')) {
        ctx.fillRect(0, 0, 800, 800);
      }
    });
  }
  paintBucket();
  let selectedColor;

  function highlightColor(node) {
    if (selectedColor) {
      selectedColor.classList.remove('highlightColor');
    }
    selectedColor = node;
    selectedColor.classList.add('highlightColor');
  }


  function choiseColor() {
    const color = document.querySelector('#color');
    color.addEventListener('mousedown', (event) => {
      const targ = event.target;


      if (targ.tagName !== 'DIV' || targ === color) return;

      highlightColor(targ);
      const currentColorEl = document.querySelector('.highlightColor');
      console.log(1);
      const currentStyles = getComputedStyle(currentColorEl);
      const currentColor = currentStyles.backgroundColor;
      ctx.fillStyle = currentColor;
    });
  }
  choiseColor();
  function duplicateFrame() {
    const duplFr = document.querySelector('.duplicate_frame');
    duplFr.onclick = function EV() {
      const currentFrame = document.querySelector('.frameChoisen');
      const cloneFrame = currentFrame.cloneNode(true);
      const UL = document.querySelector('ul');

      UL.appendChild(cloneFrame);
    };
  }
  let numberImg = 0;
  duplicateFrame();
  function animation() {
    setInterval(() => {
      const imgList = document.querySelectorAll('ul img');
      const animationBox = document.querySelector('.main-header_box--animation');
      const imgEl = document.querySelector('.main-header_box--animation img');
      if (imgEl) {
        imgEl.remove();
      }
      const imgCopy = imgList[numberImg].cloneNode(true);
      imgCopy.style.cssText = 'height: 100px; width: 100px;';
      animationBox.appendChild(imgCopy);
      // eslint-disable-next-line no-plusplus
      numberImg += 1;
      if (numberImg === imgList.length) {
        numberImg = 0;
      }
    }, 500);
  }
  animation();
}
